import { RumLoading } from '@/global/loading';
import { clearGroup, leaveGroup } from '@/service/groups/deletegroup';
import { getGroups } from '@/service/groups/getgroups';
import { useStore } from '@/store';
import { computed, defineComponent, ref } from 'vue';
export default defineComponent({
  props: {
    ifDisplayContent: {
      type: Boolean,
      require: true
    }
  },
  emits: ['update:ifDisplayContent'],

  setup(props, {
    emit
  }) {
    const deleteGroupDialog = ref(false);
    const store = useStore();
    const groups = computed(() => store.state.login.groupsInfo.groups);
    const selectDelGroupId = ref('');

    const handleMenuItemClick = group_id => {
      emit('update:ifDisplayContent', true);
      store.dispatch('main/handleGroupIdAction', group_id);
    };

    const handleDeleteGrp = group_id => {
      deleteGroupDialog.value = true;
      selectDelGroupId.value = group_id ?? '';
    };

    const handleDeleteGroupBtn = async group_id => {
      cancelDeleteGrp();
      store.commit('main/changeGroupContent', []);
      store.commit('main/clearNewGroupContent');

      const data = async () => {
        // await leaveGroup(group_id)
        // await clearGroup(group_id)
        // const groupsInfo: IGroupsInfo = await getGroups()
        // store.commit('login/changeGroupsInfo', groupsInfo)
        // await console.log('groupsInfo' + groupsInfo)
        console.log('leaveGroup' + 'before');
        leaveGroup(group_id).then(async () => {
          console.log('clearGroup' + 'after');
          console.log('clearGroup' + 'before');
          clearGroup(group_id).then(async () => {
            console.log('clearGroup' + 'after');
            const groupsInfo = await getGroups();
            store.commit('login/changeGroupsInfo', groupsInfo);
          });
        }).catch(e => console.log(e));
      };

      data();
      RumLoading(true, '...', 2000);
    };

    const cancelDeleteGrp = () => {
      deleteGroupDialog.value = false;
    };

    return {
      groups,
      handleMenuItemClick,
      handleDeleteGroupBtn,
      deleteGroupDialog,
      cancelDeleteGrp,
      handleDeleteGrp,
      selectDelGroupId
    };
  }

});