import { getGroupContent } from '@/service/content/getcontent';
import { useStore } from '@/store';
import { computed, defineComponent, reactive, ref } from 'vue';
import ContentItem from '@/components/home/cpns/content-item.vue';
import { changeGroupContent, postGroupContent } from '@/service/content/postcontent';
import { ElLoading } from 'element-plus';
import generator from 'generate-password';
export default defineComponent({
  components: {
    ContentItem
  },

  setup() {
    const store = useStore();
    const addContentForm = ref(false);
    const formLabelWidth = '80px';
    const drawerRef = ref();
    const loading = ref(false);
    const passwordLen = ref(12);
    const passwordNumbers = ref(true);
    const passwordSymbols = ref(false);
    const ifdisabled = ref(false);
    const contentForm = reactive({
      content: '',
      name: ''
    });
    const groupContent = computed(() => store.state.main.newGroupContent);

    const onClick = () => {
      drawerRef.value?.close();

      if (contentForm.content == '' || contentForm.name == '') {
        alert('');
        return;
      }

      if (ifdisabled.value === true) {
        const selectedGroupid = computed(() => store.state.main.groupId);
        let changeTrxId = '';
        getGroupContent(selectedGroupid.value).then(groupContent => {
          for (let i = groupContent.data.length - 1; i >= 0; i--) {
            if (groupContent.data[i].Content?.name == contentForm.name) {
              changeTrxId = groupContent.data[i].TrxId;
            }
          }
        }).then(() => {
          changeGroupContent(changeTrxId, selectedGroupid.value, contentForm.content, contentForm.name);
          setTimeout(() => {
            store.dispatch('main/handleGroupIdAction', selectedGroupid.value.toString());
          }, 20000);
        });
      } else {
        postGroupContent(selectGroupId.value.toString(), contentForm.content, contentForm.name);
      }

      const openFullScreen = () => {
        const loading = ElLoading.service({
          lock: true,
          text: ',',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
        }, 5000);
        setTimeout(() => {
          store.dispatch('main/handleGroupIdAction', selectGroupId.value.toString());
        }, 20000);
      };

      const data = async () => {
        getGroupContent(selectGroupId.value.toString()).then(async groupCtn => {
          store.commit('main/changeGroupContent', groupCtn);
          store.commit('main/changeGroupId', selectGroupId.value.toString());
          contentForm.content = '';
          contentForm.name = '';
          openFullScreen();
        });
      };

      data();
    };

    const changeContentItem = changeContentItemName => {
      contentForm.name = changeContentItemName;
      ifdisabled.value = true;
      addContentForm.value = true;
    };

    const cancelContenteForm = () => {
      loading.value = false;
      addContentForm.value = false;
      contentForm.content = '';
      contentForm.name = '';
      clearTimeout(1000);
    };

    const selectGroupId = computed(() => store.state.main.groupId);
    const groupContentId = reactive(getGroupContent);

    const handleAddGroupContent = () => {
      if (selectGroupId.value.toString() == '') {
        alert('');
        console.log('');
      } else {
        addContentForm.value = !addContentForm.value;
        ifdisabled.value = false;
      }
    };

    const generatePassword = function (passwordLen, passwordNumbers, passwordSymbols) {
      console.log('generatePassword');
      const password = generator.generate({
        length: passwordLen,
        numbers: passwordNumbers,
        symbols: passwordSymbols
      });
      contentForm.content = password;
    };

    return {
      formLabelWidth,
      addContentForm,
      onClick,
      loading,
      cancelContenteForm,
      contentForm,
      groupContent,
      groupContentId,
      drawerRef,
      handleAddGroupContent,
      passwordLen,
      passwordNumbers,
      passwordSymbols,
      generatePassword,
      changeContentItem,
      ifdisabled
    };
  }

});