import { useStore } from '@/store';
import { defineComponent, computed } from 'vue';
import MobContentItem from './mob-contentitem.vue';
export default defineComponent({
  components: {
    MobContentItem
  },
  props: {
    ifDisplayContent: {
      type: Boolean,
      require: true
    }
  },
  emits: ['update:handleAddContent', 'update:ifDisplayContent', 'update:ifdisabled', 'update:addContentForm', 'changeContentFormName'],

  setup(props, {
    emit
  }) {
    const store = useStore();
    const groupName = computed(() => store.state.main.groupName);
    const groupContent = computed(() => store.state.main.newGroupContent);

    const handleDisplayGroup = () => {
      emit('update:ifDisplayContent', false);
    };

    const changeContentItem = contentItemName => {
      emit('changeContentFormName', contentItemName);
      emit('update:ifdisabled', true);
      emit('update:addContentForm', true);
      emit('update:handleAddContent');
    };

    return {
      groupName,
      groupContent,
      handleDisplayGroup,
      changeContentItem
    };
  }

});