import { computed, defineComponent } from 'vue';
import Clipboard from 'clipboard';
import { ElMessage } from 'element-plus';
import { delGroupContent } from '@/service/content/delcontent';
import { useStore } from '@/store';
import { getGroupContent } from '@/service/content/getcontent';
export default defineComponent({
  props: {
    content: {
      type: Object,
      require: true
    }
  },
  emits: ['changeContentItem'],

  setup(props, {
    emit
  }) {
    const store = useStore();

    const copyContentItemBtn = () => {
      const clipboard = new Clipboard('.content-item-copybtn');
      clipboard.on('success', () => {
        ElMessage({
          center: true,
          message: '',
          type: 'success'
        }); //  

        clipboard.destroy();
      });
      clipboard.on('error', () => {
        // 
        ElMessage.error('Oops, this is a error message.'); // 

        clipboard.destroy();
      });
    };

    const changeContentItem = changeContentItemName => {
      emit('changeContentItem', changeContentItemName);
    };

    const delContentItem = (delContentItemContent, delContentItemName) => {
      const selectedGroupid = computed(() => store.state.main.groupId);
      let delTrxId = '';
      getGroupContent(selectedGroupid.value).then(groupContent => {
        for (let i = groupContent.data.length - 1; i >= 0; i--) {
          if (groupContent.data[i].Content?.content == delContentItemContent) {
            delTrxId = groupContent.data[i].TrxId;
          }
        }
      }).then(() => {
        console.log('delTrxId ' + delTrxId);
        console.log(delTrxId + ' ' + selectedGroupid.value + ' ' + delContentItemName);
        delGroupContent(delTrxId, selectedGroupid.value, delContentItemName);
        setTimeout(() => {
          store.dispatch('main/handleGroupIdAction', selectedGroupid.value.toString());
        }, 20000);
      });
    };

    return {
      copyContentItemBtn,
      delContentItem,
      changeContentItem
    };
  }

});